import { createRouter} from 'vue-router'
import { createWebHistory} from 'vue-router'
import MainPage from './views/MainPage';
import ServicesMain from './views/ServicesMain';
import ServicesBooks from './views/ServicesBooks';
import ServicesGenResearch from './views/ServicesGenealogResearch';
import ServicesFamilyTrees from './views/ServicesFamilyTrees';
import ServicesMilitarySearch from './views/ServicesMilitarySearch';
import CompanyHistory from './views/CompanyHistory';
import OurProgress from './views/OurProgress';
import PhotoGallery from './views/PhotoGallery';
import GalleryAlbum from './views/GalleryAlbum';
import CorpArticles from './views/CorpArticles';
import CorpArticlesNews from './views/CorpArticlesNews';
import NotFound from './views/NotFound';

const router = createRouter({
    base: '/home',
    mode: 'hash',
    history: createWebHistory(),  //      
    routes: [
        { path: '/:pathMatch(.*)*', redirect: '/home'}, 
        {   
            path: '/home',
            component: MainPage,
            name: 'home',
            meta: {
                title: '   |        ',
                desc: '          ,      ,    ,   ,     .  . ! 8(347)224-28-99'
            }
        },   // localhost:port/home  ***** alias: '/'
        {
            path: '/services',
            component: ServicesMain,
            name: 'services',
            meta: {
                title: '   -     | ',
                desc: '      ,  ,  ,  ,  ,   .  . ! 8(347)244-28-99'
            }
        }, 
        {
            path: '/services/books',
            component: ServicesBooks,
            name: 'books',
            meta: {
                title: '        | ',
                desc: '           -     .  . ! 8(347)224-28-99'
            }
        },
        {
            path: '/services/family_trees',
            component: ServicesFamilyTrees,
            name: 'family_trees',
            meta: {
                title: '  -     | ',
                desc: '   -     .       -   .   . ! 8(347)224-28-99'
            }
        },
        {
            path: '/services/genealogical_research',
            component: ServicesGenResearch,
            name: 'genealogical_research',
            meta: {
                title: '  ,  | ',
                desc: '       ,     100       .  . ! 8(347)224-28-99'
            }
        },
        {
            path: '/services/military_search',
            component: ServicesMilitarySearch,
            name: 'military_search',
            meta: {
                title: '      | ',
                desc: '      ,     .       .   ,  -       .  . ! 8(347)224-28-99'
            }
        },
        {
            path: '/articles',
            component: CorpArticles,
            name: 'articles',
            meta: {
                title: '       | ',
                desc: '  ,     ().         ()  ().  . ! 8(347)244-28-99'
            }
        },
        {
            path: '/about_company/company_history',
            component: CompanyHistory,
            name: 'company_history',
            meta: {
                title: '  ',
                desc: '    ""  -   -, ,    ,     ,        ,           .  . ! 8(347)244-28-99'
            }
        },
        {
            path: '/about_company/progress',
            component: OurProgress,
            name: 'progress',
            meta: {
                title: '    ',
                desc: '          ,          .'
            }
        },
        {
            path: '/about_company/photo_gallery',
            component: PhotoGallery,
            name: 'photo_gallery',
            meta: {
                title: '   ',
                desc: '         (  )      ,  ,    .  . ! 8(347)244-28-99'
            }
        },
        {
            path: '/about_company/photo_gallery/album/:albumId?',
            component: GalleryAlbum,
            name: 'gallery_album',
            meta: {
                title: '',
                desc: ''
            }
        },
        { path: '/articles/news/:newsId?', component: CorpArticlesNews, name: 'articles-news'},
        { path: '/:notFound(.*)*', component: NotFound},
        //{ path: '/:notFound(.*)', component: NotFound}        
        //{ path: '/forget', component: AppForget, meta: {cantEnter: true}},
        // { path: '/mail/:mailId?', component: AppMail}, //    
        // { path: '/:notFound(.*)', redirect: '/login'}
        // { path: '/:notFound(.*)', redirect: '/home'}
    ],
    linkActiveClass: 'active',  //   'router-link-active'
    linkExactActiveClass: 'active'  //   'router-link-exact-active'
})

router.beforeEach((to, from, next) => {
    document.title = `${to.meta.title}`;
    let the_description = document.querySelector('meta[name="description"]');
    the_description.setAttribute("content", `${to.meta.desc}`);
    next();
})

// router.afterEach((to, from) => {
//     //  ,        
//     console.log('TO afterEach', to)
//     console.log('FROM afterEach', from)
// })



export default router